/* Tomorrow Night Theme */


/* http://jmblog.github.com/color-themes-for-google-code-highlightjs */


/* Original theme - https://github.com/chriskempson/tomorrow-theme */


/* http://jmblog.github.com/color-themes-for-google-code-highlightjs */


/* Tomorrow Comment */

.hljs-comment,
.hljs-quote {
	color: #969896;
}


/* Tomorrow Red */

.hljs-variable,
.hljs-template-variable,
.hljs-tag,
.hljs-name,
.hljs-selector-id,
.hljs-selector-class,
.hljs-regexp,
.hljs-deletion {
	color: #cc6666;
}


/* Tomorrow Orange */

.hljs-number,
.hljs-built_in,
.hljs-builtin-name,
.hljs-literal,
.hljs-type,
.hljs-params,
.hljs-meta,
.hljs-link {
	color: #de935f;
}


/* Tomorrow Yellow */

.hljs-attribute {
	color: #f0c674;
}


/* Tomorrow Green */

.hljs-string,
.hljs-symbol,
.hljs-bullet,
.hljs-addition {
	color: #b5bd68;
}


/* Tomorrow Blue */

.hljs-title,
.hljs-section {
	color: #81a2be;
}


/* Tomorrow Purple */

.hljs-keyword,
.hljs-selector-tag {
	color: #b294bb;
}



.hljs {
	background: $code-background;
	border-radius: 3px;
	color: #c5c8c6;
	display: block;
	font-family: $font-code;
	font-size: $code-font-size;
	margin-bottom: 1.5em;
	overflow-x: auto;
	padding: 1em;
}

.hljs-emphasis {
	font-style: italic;
}

.hljs-strong {
	font-weight: bold;
}